@import "../../global.scss";

.navbar {
    width: 100%;
    height: 72px;
    background: linear-gradient(90deg, rgb(26, 23, 23) 0%, rgb(151, 1, 1) 100%);
    color: whitesmoke;
    position: fixed;
    top: 0;
    transition: all 1s ease;
    z-index: 3;

    


    .wrapper {
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left {
            display: flex;
            align-items: center;

            .logo{
                font-size: 40px;
                font-weight: 700;
                text-decoration: none;
                color: inherit;
                margin-right: 40px;
            }

            .itemContainer {
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile {
                    display: none;
                }

                .icon {
                    font-size: 21px;
                    margin-right: 5px;
                    color: goldenrod;
                }
                

                a {
                    font-size: 17px;
                    color: whitesmoke;
                    // font-weight: 700;
                    cursor: pointer;
                }

                a:hover {
                    font-weight: 600;
                }
            }

            
        }

        .right {
            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
    
                span {
                    width: 100%;
                    height: 3px;
                    background-color: goldenrod;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }

    a { 
        color: whitesmoke;
        text-decoration: none;
    }
    
    span {
        color: rgb(194, 1, 1);
        font-family: 'Rouge Script', cursive;
    }

    &.active {
        background: black!important;
        color: whitesmoke;
        

        .hamburger {
            span{
                &:first-child{
                    background-color: goldenrod;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color: goldenrod;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}




