@import "../../global.scss";

.hero {
  background: linear-gradient(90deg, rgb(26, 23, 23) 3%, rgb(151, 1, 1) 100%);
  color: whitesmoke;
  display: flex;

  @include mobile {
    flex-direction: column;
    align-items: center;
  }

  .left {
    flex: 0.5;
    overflow: hidden;

    .imgContainer {
      width: 700px;
      height: 700px;
      background-color: black;
      border-radius: 50%;
      margin-top: 45px;
      margin-left: 30px;
      overflow-x: hidden;

      @include mobile {
        align-items: flex-start;
      }

      img {
        // CTV Radio Logo
        height: 90%;
        margin-left: 45px;
        margin-top: 30px;

        @include mobile {
          height: 50%;
        }
      }
    }
  }

  .right {
    flex: 0.5;
    position: relative;
    background-image: url("../../../public/assets/ctvlogotran.jpg");

    .wrapper {
      width: 100%;
      height: 100%;

      padding-left: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;

      @include mobile {
        padding-left: 0;
        align-items: center;
      }

      .tiktok {
        margin-bottom: 100px;
        margin-right: 40px;

        @include mobile {
          font-size: 40px;
        }
      }

      button {
        font-size: 40px;
        font-family: "Rouge Script", cursive;
        background-color: transparent;
        border: 1px solid goldenrod;
        transition: all 0.3s ease-out;
        border-radius: 5%;
        cursor: pointer;
        margin-bottom: 50px;
        margin-left: 675px;
        background-color: goldenrod;
        text-decoration: none;
        color: rgb(133, 4, 4);
      }

      button:hover {
        font-weight: 800;
      }
    }

    a {
      position: absolute;
      bottom: 10px;
      left: 2%;

      img {
        width: 50px;
        animation: arrowBlink 2s infinite;
      }

      @keyframes arrowBlink {
        100% {
          opacity: 0;
        }
      }
    }
  }
}
